class Timer extends React.Component {
    constructor(props){
        super(props);
        this.state = {show: false}
    }

    componentDidMount(){
        this.timerID = setTimeout(
            () => this.tick(), this.props.seconds*1000
        );
    }

    componentWillUnMount(){
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({
            show: true
        });
    }

    render(){
        return <h2>{this.state.show ? "hey" : ""}</h2>
    }
}
Timer.defaultProps = {
    seconds: 25
};

ReactDOM.render(
    <Timer seconds="10"></Timer>
    ,
    document.getElementById('root')
);